<template>
    <ul class="nav-wrap">
        <router-link v-for="menu in menuList" :to="menu.target" :key="menu.target">
        <li class="nav" :class="menu.status">
            <dl :class="menu.title===''?'map':''">
                <dt>{{menu.title}}</dt>
                <dd>{{menu.profile}}</dd>
            </dl>
            <i class="iconfont" :class="'icon-'+menu.icon">{{menu.status==='frozen'?'工事中，敬请期待_(:3」∠)_':''}}</i>
            
        </li>
        </router-link>
    </ul>
</template>

<script>
/* eslint-disable */
export default {
    name:'Nav',
    data () {
        return { 
            menuList:[
                { title:'Studio Layout', target:'layout', profile:'布景展示', icon:'layout', status:'active' },
                { title:'Samples', target:'sample', profile:'客片展示', icon:'sample', status:'active' },
                { title:'Reserve', target:'reservation', profile:'预约拍摄', icon:'_reserve', status:'frozen' },
                { title:'', target:'map', profile:'我们在哪？', icon:'map', status:'active' },
                // { title:'Anime Conventions', target:'showInfo', profile:'漫展情报', status:'frozen' },
                // { title:'旅拍攻略', target:'travel', profile:'', status:'frozen' },
                // { title:'迷之黑洞', target:'blackHole', profile:'', status:'frozen' },
            ]
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../assets/styles/theme';
.nav-wrap{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-wrap: wrap;
    .nav{
        width: 200px;
        height: 170px;
        margin: 6px;
        background: rgba(255, 255, 255, 0.18);
        cursor: pointer;
    }
}
.nav{
    position: relative;
    transition: all .2s ease-out;
    font-weight: 100;
    font-family: sans-serif;
    &:hover{
        box-shadow: 0px 0px 10px 2px rgba(255, 181, 95, 0.62);
        &>.iconfont{
            bottom: 10px;
            opacity: 1;
        }
    }
    &>dl{
        &>dt{
            margin-top: 16px;
            padding: 10px;
            font-size: 24px;
        }
        &>dd{
            padding: 0 10px;
            text-align: right;
            font-size: 16px;
        }
        &.map{
            &>dt{
                margin-top: 0;
                height: 100px;
                background: url('../../assets/images/home/map.jpg');
                background-size: contain;
            }
            &>dd{
                margin-top: 15px;
                padding: 0 0 0 10px;
                text-align: left;
            }
        }
    }
    &>.iconfont{
        position: absolute;
        bottom: 30px;
        right: 10px;
        display: inline-block;
        opacity: 0;
        font-size: 1.5rem;
        transition: all .2s ease-out;
    }
    &.active{
        &>dl{
            &>dt{
                color: map-get($theme-Europe-grass, secondary);
                &::first-letter{
                    color: map-get($theme-Europe-grass, main)
                }
            }
            &>dd{
                color: map-get($theme-Europe-grass, main)
            }
        }
        &:hover{
            transform: scale(1.1);
            &>.iconfont{
                color: map-get($theme-Europe-grass, main);
            }
        }
    }
    &.frozen{
        &>dl{
            &>dt{
                color: #dcdcdc;
                &::first-letter{
                    color: #999;
                }
            }
            &>dd{
                color: #aaa;
            }
        }
        &>.iconfont{
            font-size: 12px;
            color: #dcdcdc;
        }
    }
}


</style>


