<template>
    <div class="main-content" :style="{'height': mainHeight + 'px'}">
        <aside class="brief-info">
            <h1 class="title">NNF</h1>
            <h2 class="second-title">摄影工作室</h2>
            <img class="head" src="../../assets/images/home/nmk_req20120.png" alt="nnf...">
            <p class="date">今天是<br>{{today}}</p>
            <p class="weather">
                <em class="temp"></em>
                <i class="iconfont" :class="weather"></i>
            </p>
            <p class="description">白菜小破棚<br>欢迎约拍ヽ(ﾟ∀ﾟ)ﾉ</p>
            <p class="contact">
                <i class="iconfont weibo"></i>
            </p>
        </aside>
        <Nav></Nav>
    </div>
</template>

<script>
/* eslint-disable */
import Nav from './Nav'
export default {
    name:'Home',
    components: { Nav },
    data(){
        return {
            mainHeight: window.innerHeight
        }
    },
    computed:{
        today(){
            const nowDate = new Date()
            return `${nowDate.getFullYear()} 年 ${nowDate.getMonth()+1} 月 ${nowDate.getDate()} 日`
        }
    },
    mounted(){
        const _this = this
        window.onresize = () => {
            _this.mainHeight = window.innerHeight
        }
    }
}
</script>

<style lang="scss" scoped>

.brief-info{
    position: absolute;
    right: 6%;
    top: 10%;
    width: 200px;
    height: 500px;
    padding: 20px 10px;
    color:#dec7ad;
    text-align: right;
    font-weight: 100;
    font-family: sans-serif;
    .head,.date,.description,.contact{
        margin: 1rem 0;
    }
    
}
</style>


